import React from 'react';



const Objetivo = ({ imgObjetivo }) => {
    return (
        <div className="value">
            <div className="valueDescription">
                <p><strong>Objetivo: </strong><br />
                Contribuir a la capacitacin tcnica y cientfica de los asociados posibilitando un mayor conocimiento del desarrollo y tcnicas aplicadas mediante las comisiones creadas ad hoc. <br/>
                Gestionar la registracin y/o habilitacin de sus socios para ser presentados y reconocidos por los organismos pertinentes vigentes oficiales y privados.<br/>
                Procurar el nucleamiento de todos los profesionales electricistas y afines de los distintos niveles de la actividad que desempeen tareas tcnicas y de servicios derivadas de la misma en la provincia de Crdoba, para contribuir al desarrollo, prosperidad e intercambio de sus asociados.<br/>
                Brindar asesoramiento y/o asistencia a sus asociados en el orden tcnico propiamente dicho, administrativo contable, jurdica, y de seguridad en el ejercicio de la profesin.<br/>
                Operar en todo el territorio nacional e internacional, vinculndose a tales fines con las autoridades publicas y/o instituciones privadas que correspondan.<br/>
                </p>
            </div>
            <div className="valueImg">
                <img className="imgValues" src={imgObjetivo} />
            </div>
        </div>
    );
}

export default Objetivo;
