import React from 'react';
import "./../styles/AboutUsHistory.css";
import Gallery from "./Gallery";

const AboutUsHistory = () => {
    return (
        <div className="cnt flxBxClumn cntHistory">
            <div class="cnt-title">
                <h2 className="title">Sobre nosotros</h2>
                <div class="border" />
            </div>
            <div className="cnt-content">
                    <div>
                        <p>EPAC, una Asociacin Civil sin
                        fines de lucro, conformada por profesionales relacionados a la energa elctrica.
                        La Comisin Directiva est integrada por profesionales universitarios
                        docentes, tcnicos docentes e instaladores electricistas categora III. Todos trabajando en el nuevo
                        paradigma de la energa elctrica de acuerdo a las nuevas leyes y bajo reglamentacin de AEA y
                        normas IRAM.

                        EPAC cuenta con una trayectoria de ocho aos dando charlas, cursos y
                        congresos de seguridad elctrica, electricidad domiciliaria e industrial y energa renovable.
                        Articula con empresas y comercios del mercado elctrico y ha realizado
                    publicaciones en la revista y programa ElectroInstalador de BS AS.</p>
                    </div>
                <div className="containerGalery">
                    <Gallery />
                </div>
            </div>

        </div>
    );
}

export default AboutUsHistory;