import React from 'react';


const Mision = ({ imgMission }) => {
    return(
        <div className="value">
            <div className="valueDescription">
                <p><strong>Misin: </strong><br/>
                Desarrollar actividades directamente relacionadas con el
                rea elctrica y complementarias con reas afines como
                electrnica, electromecnica, informtica, robtica, domtica,
                industrial, telecomunicaciones, electromdicas, etc., en los
                aspectos culturales, asistenciales, educativos, de formacin
                tcnica y seguridad de personas, animales, bienes y medio
                ambiente, conforme a las normativas, reglamentaciones, y
                disposiciones tcnicas legales vigentes municipales,
                provinciales, nacionales e internacionales en caso de existir
                omisin en las mencionadas normativas locales.</p>
            </div>
            <div className="valueImg">
                <img className="imgValues" src={imgMission}/>
            </div>
        </div>
    );
}

export default Mision;
